body{
  background-color: #7e57c2;
}
.App{
  background-color:#0c2165;
  min-height:500px ;
  width: 400px;
  margin: 40px auto; 
}
#to-do-form input{
  height: 50px;
  width: 200px;
  background-color:#00838f;
  border: 0;
  padding: 0 20px;
  margin: 20px;
  font-size: 18px;
  border-radius: 10px;
  

}

#to-do-form input :focus{
  outline: none;
}

#to-do-form button{
  height: 50px;
  width: 80px;
  background-color: yellow;
  border: 0;
  border-radius: 10px;
  cursor: pointer;
  font-weight: bolder;
}